body {
  margin: 0;
  padding: 0;
}



.wrapper {
  background: rgba(211, 211, 211, 0.733);
}
.jumbotron {
  background-color: rgb(28, 197, 253);
}

 .images {
    height: 170px;
    width: 170px;
    object-fit:scale-down;
    overflow: hidden;
    padding: 10px 5px 10px 5px;
 }

 .images:hover {
   -webkit-transform: scale(1.1);
   transform: scale(1.1);
 }

@media (min-width: 1200px) {
  .container {
      max-width: 900px;
  }
}

.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 40px;
  line-height: 40px;
  margin-top: 60px;
}
